import React, { Component } from 'react';
import {PageHeader} from 'react-bootstrap';
import { Card, Button, CardImg, CardTitle, CardText, CardDeck,
 CardSubtitle, CardBody ,CardLink} from 'reactstrap';
import axios from 'axios';
import './App.css';
import { Link } from 'react-router-dom';
import {Row,Col,Grid} from 'react-bootstrap';
import {Timeline, TimelineEvent} from 'react-event-timeline';
import {Modal,Table,tr,thead,tbody,td} from 'react-bootstrap';
class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      notes:[],
      timelines:[],
      show:false,
      modalTitle : "",
      modalDesc : ""
    }
    this.groupBy = this.groupBy.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleShow = this.handleShow.bind(this);
  }
  componentDidMount(){
    axios('/notes').then(res => {
      var results = this.groupBy("journal_date",res.data);
      this.setState({
        notes : res.data,
        timelines:results
       });
      debugger;
      console.log(this.state.notes);
    });
  }
  handleClose() {
    debugger;
    this.setState({
      show: false,
      modalTitle:"",
      modalDesc:""
     });
  }

  handleShow(note) {
    this.setState({
      show: true,
      modalTitle:note.title,
      modalDesc:note.message
    });
  }
 groupBy(key, array) {
   debugger;
  var result = [];
  for (var i = 0; i < array.length; i++) {
    var added = false;
    for (var j = 0; j < result.length; j++) {
      if (result[j][key] == array[i][key]) {
        result[j].items.push(array[i]);
        added = true;
        break;
      }
    }
    if (!added) {
      var entry = {items: []};
      entry[key] = array[i][key];
      entry.items.push(array[i]);
      result.push(entry);
    }
  }
  return result;
}
  render() {
    return (
      <div className="container app">
          <div className="page-header-row">
            <h2 className="page-header">Journal 308
            <img url="./us.jpg"></img>
            </h2>

          </div>
          <Row className="action-button-row">
            <Link className="add-entry-sh-btn" to={{ pathname: '/create', state: { className: 'note_shivani'} }}><span className="glyphicon glyphicon-plus-sign" aria-hidden="true"></span>New Entry - Shivani</Link>
            <Link className="add-entry-so-btn" to={{ pathname: '/create', state: { className: 'note_sourabh'} }}><span className="glyphicon glyphicon-plus-sign" aria-hidden="true"></span>New Entry - Sourabh</Link>
            <hr className="brace"></hr>
          </Row>
          <Row>
            <Timeline className="timeline">
              {this.state.timelines.map((timeline,index)=>
                <TimelineEvent
                  key={index}
                  title={timeline.journal_date}
                  icon={<i />}
                  iconColor="#6fba1c"
                  className="timeline-event" >
                {timeline.items.map((note,itr)=>
                  <Col xs={12} md={6} lg={3} className="timeline-event-col" key={itr}>
                    <Card className={note.className} onClick={this.handleShow.bind(this,note)}>
                      <CardBody>
                        <div dangerouslySetInnerHTML={{__html: note.title}}></div>
                      </CardBody>
                      <CardBody>
                        <Row>
                          <div>

                          </div>
                        </Row>
                      {
                        //  <CardText>{note.message}</CardText>
                        // <CardLink href="#">Card Link</CardLink>
                        // <CardLink href="#">Another Link</CardLink>
                      }
                      </CardBody>
                    </Card>
                  </Col>

                )}

              </TimelineEvent>
              )}
              </Timeline>
          </Row>
          <Row>
            <div>
              <Modal show={this.state.show} className="modal-popover" onHide={this.handleClose.bind(this)}>
                  <Modal.Header closeButton className="modal-title">
                    <Modal.Title >
                      {
                         <div  dangerouslySetInnerHTML={{__html: this.state.modalTitle}}></div>
                      }
                    </Modal.Title>
                  </Modal.Header>
                  <Modal.Body className="container-fluid modal-desc">
                    <div>
                        {
                           <div dangerouslySetInnerHTML={{__html: this.state.modalDesc}}></div>
                          }
                    </div>
                  </Modal.Body>
              </Modal>
            </div>
          </Row>
        </div>
    );
  }
}
// <Row className="page-header-row">
//   <h2 className="page-header">Journal 308
//
//   </h2>
//
// </Row>
export default App;
