import React,{Component} from 'react';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.bubble.css';
import axios from 'axios';
import {Row,Col} from 'react-bootstrap';
import { Button } from 'reactstrap';
import moment from 'moment';
class CreateEntry extends Component{
  constructor(props){
    super(props);
    this.state = {
      journal_date : moment().format('ll'),
      title : "",
      message : "",
      reaction : ""
   }
    this.titleChange = this.titleChange.bind(this);
    this.messageChange = this.messageChange.bind(this);
    this.fnSaveEntry = this.fnSaveEntry.bind(this);
  }
  titleChange(value) {

    this.setState({ title: value })
  }
  messageChange(value){
    this.setState({ message: value })
  }
  componentDidMount(){

  }
  fnSaveEntry(){
    const{className} = this.props.location.state;
    const {journal_date, title, message,reaction} = this.state;
    axios.post('/notes', { journal_date, title, message,reaction,className })
     .then(result => {
       this.props.history.push("/")
     });
  }
  render(){
    return (
      <div className="container-fluid create-entry">
      <Row>
        <div className="new-note-title">Title</div>
        <ReactQuill className="title-editor" theme="bubble" placeholder="Describe ur day in 3 words or less!" value={this.state.title || " "}
                  onChange={this.titleChange} >
        </ReactQuill>
      </Row>
      <Row>
        <div className="new-note-message">Message</div>
          <ReactQuill className="message-editor" theme="bubble" placeholder="Hey beautiful, how was ur day!" value={this.state.message}
                    onChange={this.messageChange} />
      </Row>
        <Button className="new-note-save-btn" color="success" onClick={this.fnSaveEntry}>Save</Button>
      </div>
    );
  }
}
export default CreateEntry;
